import React, { Component } from 'react';
import { Hasil, ListCategories, Menus } from '../Component'
import { Row, Col } from 'react-bootstrap'
import { API_URL } from '../Utils/Constants'
import axios from 'axios'
import swal from 'sweetalert'



export default class Home extends Component {
    constructor(props) {
        super(props)

        this.state = {
            menus: [],
            categoriYangDipilih: "Makanan",
            keranjangs: []
        }
    }

    componentDidMount() {
        axios.get(API_URL + "products?category.nama=" + this.state.categoriYangDipilih)
            .then(res => {
                const menus = res.data;
                this.setState({ menus });
            })
            .catch(error => {
                console.log(Error)
            });


        axios.get(API_URL + "keranjangs")
            .then(res => {
                const keranjangs = res.data;
                this.setState({ keranjangs });
            })
            .catch(error => {
                console.log(Error)
            });
    }


    componentDidUpdate(prevState) {
        if (this.state.keranjangs !== prevState.keranjangs) {
            axios.get(API_URL + "keranjangs")
                .then(res => {
                    const keranjangs = res.data;
                    this.setState({ keranjangs });
                })
                .catch(error => {
                    console.log(Error)
                });
        }
    }



    changeCategory = (value) => {
        this.setState({
            categoriYangDipilih: value,
            menus: []
        })
        axios.get(API_URL + "products?category.nama=" + value)
            .then(res => {
                const menus = res.data;
                this.setState({ menus });
            })
            .catch(error => {
                console.log(Error)
            })
    }






    masukKeranjang = (value) => {
        axios.get(API_URL + "keranjangs?product.id=" + value.id)
            .then(res => {
                if (res.data.length === 0) {
                    const keranjang = {
                        jumlah: 1,
                        total_harga: value.harga,
                        product: value
                    };

                    axios.post(API_URL + "Keranjangs", keranjang)
                        .then(res => {
                            swal({
                                title: "Sukses Masuk Keranjang",
                                text: "Sukses Masuk Keranjang" + keranjang.product.nama,
                                icon: "success",
                                buttons: false,
                                timer: 1500,
                            });
                        })
                        .catch(error => {
                            console.log(Error)
                        })
                }


                else {
                    const keranjang = {
                        jumlah: res.data[0].jumlah + 1,
                        total_harga: res.data[0].total_harga + value.harga,
                        product: value
                    };
                    axios.put(API_URL + "Keranjangs/" + res.data[0].id, keranjang)
                        .then(res => {
                            swal({
                                title: "Sukses Masuk Keranjang",
                                text: "Sukses Masuk Keranjang" + keranjang.product.nama,
                                icon: "success",
                                buttons: false,
                                timer: 1500,
                            });
                        })
                        .catch(error => {
                            console.log(Error)
                        })
                }
            })
            .catch(error => {
                console.log(Error)
            })

    }

    render() {
        const { menus, categoriYangDipilih, keranjangs } = this.state
        return (
            <div>
                <div className="mt-5">
                    <div className="container-fluid">
                        <Row>
                            <ListCategories changeCategory={this.changeCategory} categoriYangDipilih={categoriYangDipilih} />
                            <Col>
                                <h4>Daftar Produk</h4>
                                <hr />
                                <Row className="overflow-auto menu">
                                    {menus && menus.map((menu) => (
                                        <Menus
                                            key={menu.id}
                                            menu={menu}
                                            masukKeranjang={this.masukKeranjang}
                                        />
                                    ))}
                                </Row>
                            </Col>
                            <Hasil keranjangs={keranjangs} {...this.props} />
                        </Row>
                    </div>
                </div>
            </div>
        );
    }
}

