import React, { Component } from 'react';
import { Col, ListGroup } from 'react-bootstrap';
import axios from 'axios'
import { API_URL } from '../Utils/Constants'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faUtensils, faCoffee, faCheese } from '@fortawesome/free-solid-svg-icons'



const Icon = ({ nama }) => {
    if (nama === "Makanan") return <FontAwesomeIcon icon={faUtensils} className="mr-2" />
    if (nama === "Minuman") return <FontAwesomeIcon icon={faCoffee} className="mr-1" />
    if (nama === "Cemilan") return <FontAwesomeIcon icon={faCheese} className="mr-2" />

}
class ListCategories extends Component {
    constructor(props) {
        super(props)

        this.state = {
            categories: []
        }
    }
    componentDidMount() {
        axios.get(API_URL + "categories")
            .then(res => {
                console.log(res)
                const categories = res.data;
                this.setState({ categories });
            })
            .catch(error => {
                console.log(Error)
            })
    }

    render() {
        const { categories } = this.state
        const { changeCategory, categoriYangDipilih } = this.props
        return (
            <Col md="3" mt="2">
                <h4><strong>Daftar Kategori</strong></h4>
                <hr />
                <ListGroup>
                    {categories && categories.map((category) => (
                        <ListGroup.Item key={category.id} onClick={() => changeCategory(category.nama)}
                            className={categoriYangDipilih === category.nama && "category-active"} >
                            <h5 className="list-categories">
                                <Icon nama={category.nama} />{category.nama}</h5>
                        </ListGroup.Item>
                    ))}
                </ListGroup>
            </Col >
        );
    }
}

export default ListCategories;