import React, { Component } from 'react';
import { Col, ListGroup, Row, Badge, Card } from 'react-bootstrap';
import { numberWithCommas } from '../Utils/Utils';
import ModalKeranjang from './ModalKeranjang';
import TotalBayar from './TotalBayar';
import { API_URL } from '../Utils/Constants'
import axios from 'axios'
import swal from 'sweetalert'
class Hasil extends Component {

    constructor(props) {
        super(props)

        this.state = {
            showModal: false,
            keranjangDetail: false,
            jumlah: 0,
            keteranagn: '',
            totaHarga: 0,
        }
    }

    handleShow = (menuKeranjang) => {
        this.setState({
            showModal: true,
            keranjangDetail: menuKeranjang,
            jumlah: menuKeranjang.jumlah,
            keteranagn: menuKeranjang.keteranagn,
            totalHarga: menuKeranjang.total_harga
        })
    }
    handleClose = () => {
        this.setState({
            showModal: false
        })
    };

    tambah = () => {
        this.setState({
            jumlah: this.state.jumlah + 1,
            totalHarga: this.state.keranjangDetail.product.harga * (this.state.jumlah + 1)
        })
    }

    kurang = () => {
        if (this.state.jumlah !== 1) {
            this.setState({
                jumlah: this.state.jumlah - 1,
                totalHarga: this.state.keranjangDetail.product.harga * (this.state.jumlah - 1)
            })
        }
    }

    changeHandler = (event) => {
        this.setState({
            keteranagn: event.target.value,

        })
    }
    handleSubmit = (event) => {
        event.preventDefault();
        this.handleClose();

        const data = {
            jumlah: this.state.jumlah,
            total_harga: this.state.totaHarga,
            product: this.state.keranjangDetail.product,
            keteranagn: this.state.keteranagn
        };

        axios.put(API_URL + "Keranjangs/" + this.state.keranjangDetail.id, data)
            .then(res => {
                swal({
                    title: "Update Pesanan",
                    text: "Sukses Update Pesanan" + data.product.nama,
                    icon: "success",
                    buttons: false,
                    timer: 1500,
                });
            })
            .catch(error => {
                console.log(Error)
            })
    }

    hapusPesanan = (id) => {
        this.handleClose();
        axios.delete(API_URL + "Keranjangs/" + id)
            .then(res => {
                swal({
                    title: "Hapus Pesanan",
                    text: "Sukses Update Pesanan" + this.state.keranjangDetail.product.nama,
                    icon: "error",
                    buttons: false,
                    timer: 1500,
                });
            })
            .catch(error => {
                console.log(Error)
            })
    }
    render() {
        const { keranjangs } = this.props
        return (
            <Col md={3} mt="2">
                <h6><strong>Hasil</strong></h6>
                <hr />
                {keranjangs.length !== 0 && (
                    <Card className="overflow-auto hasil">
                        <ListGroup variant="flush">
                            {keranjangs.map((menuKeranjang) => (
                                <ListGroup.Item key={menuKeranjang.id} onClick={() => this.handleShow(menuKeranjang)}>
                                    <Row>
                                        <Col>
                                            <h4>
                                                <Badge pill variant="success" >
                                                    {menuKeranjang.jumlah}
                                                </Badge>
                                            </h4>
                                        </Col>
                                        <Col>
                                            <h4>{menuKeranjang.product.nama}</h4>
                                            <p>Rp. {numberWithCommas(menuKeranjang.product.harga)}</p>
                                        </Col>
                                        <Col>
                                            Rp. {numberWithCommas(menuKeranjang.total_harga)}
                                        </Col>
                                    </Row>
                                </ListGroup.Item>
                            ))}

                            <ModalKeranjang handleClose=
                                {this.handleClose}
                                {...this.state}
                                tambah={this.tambah}
                                kurang={this.kurang}
                                changeHandler={this.changeHandler}
                                handleSubmit={this.handleSubmit}
                                hapusPesanan={this.hapusPesanan}
                            />
                        </ListGroup>
                    </Card>
                )}
                <TotalBayar keranjangs={keranjangs} {...this.props} />
            </Col>
        );
    }
}

export default Hasil;