import React from 'react'
import { Card} from 'react-bootstrap'
import { numberWithCommas } from '../Utils/Utils'


const Menus = ({ menu, masukKeranjang }) => {
    return (
        <div className="col-md-4 mb-5">
            <Card className="shadow" style={{ width: '18rem' }} onClick={() => masukKeranjang(menu)}>
                <Card.Img variant="top"
                    // src ={
                    //     "assets/images/" +
                    //      menu.category.nama.toLowerCase() + 
                    //      "/" + 
                    //      menu.gambar 
                    //     }                    
                src="http://2.bp.blogspot.com/-dskRrIAukeQ/UOvC0ZgyZyI/AAAAAAAAAHg/FpxNX53OW7M/s1600/sate-ayam-bumbu-kacang.jpg" alt=""
                />
                <img src="public/assets/alen/pangsit.jpg" alt="" />
                <Card.Body>
                    <Card.Title>{menu.nama} <strong> ({menu.kode}</strong> )</Card.Title>
                    <Card.Text>
                        Rp.{numberWithCommas(menu.harga)}
                    </Card.Text>
                </Card.Body>
            </Card>
        </div>
    )
}

export default Menus
