import React from 'react'
import { Nav, Navbar, NavDropdown } from 'react-bootstrap'

const NavbarComponent = () => {
    return (
        <div>
            <Navbar variant="dark" expand="lg">
                <div className="container">
                    <Navbar.Brand href="#home">Kasir App</Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="ml-auto">
                            <Nav.Link className="lega" href="#home">Home</Nav.Link>
                            <Nav.Link className="lega" href="#link">Link</Nav.Link>
                            <NavDropdown className="lega drop" title="Dropdown" id="basic-nav-dropdown">
                                <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                                <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                                <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                                <NavDropdown.Divider />
                                <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
                            </NavDropdown>
                        </Nav>
                    </Navbar.Collapse>
                </div>
            </Navbar>
        </div>
    )
}

export default NavbarComponent
